import './product-list-page.scss';
import BasePage from '../BasePage/base-page';
import ProductListContainer from '../../containers/ProductListContainer/product-list-container';
import React, { useState } from 'react';

const ProductListPage = () => {
  const [isLoaded, setIsLoaded] = useState(false);
  return (
    <BasePage isLoading={!isLoaded}>
      <ProductListContainer onSetIsLoaded={value => setIsLoaded(value)} />
    </BasePage>
  );
};

export default ProductListPage;
