import lodash from 'lodash';
import { useEffect, useState } from 'react';

export const useWindowDimension = () => {
  const [dimension, setDimension] = useState([window.innerWidth, window.innerHeight]);
  useEffect(() => {
    const debouncedResizeHandler = lodash.debounce(() => {
      setDimension([window.innerWidth, window.innerHeight]);
    }, 100);
    window.addEventListener('resize', debouncedResizeHandler);
    return () => window.removeEventListener('resize', debouncedResizeHandler);
  }, []);
  return dimension;
};

// useDarkMode
let isDarkMode = localStorage.getItem('isDarkMode') === 'true';
const listeners = new Set();
const setIsDarkMode = () => {
  isDarkMode = !isDarkMode;
  localStorage.setItem('isDarkMode', isDarkMode);
  listeners.forEach(listener => listener());
};
export const useDarkMode = () => {
  const [state, setState] = useState(isDarkMode);
  useEffect(() => {
    const listener = () => {
      setState(isDarkMode);
    };
    listeners.add(listener);
    listener(); // in case it's already changed
    return () => listeners.delete(listener); // cleanup
  }, []);
  return [state, setIsDarkMode];
};

export default () => {};
