import './base-page.scss';
import AnnouncementBar from '../../../component/AnnouncementBar/announcement-bar';
import LoadingOverlay from 'react-loading-overlay';
import NavBar from '../../../component/NavBar/nav-bar';
import PageFooter from '../../../component/PageFooter/page-footer';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useDarkMode } from '../../../hooks/bubuchichis-hook';
// eslint-disable-next-line
const loadingGif = require('../../../img/lowres_loading.gif');

LoadingOverlay.propTypes = undefined;
const BasePage = ({ children, isLoading }) => {
  const isAnnouncementActive = true;
  const isDarkMode = useDarkMode()[0];
  return (
    <LoadingOverlay
      active={isLoading}
      spinner={
        <img
          src={loadingGif}
          style={{ maxWidth: '50%' }}
          alt="loading..."
        />
      }
      >
      <div className={`theme--${isDarkMode ? 'dark' : 'default'}`}>
        <div className="base-page">
          { isAnnouncementActive && (
            <AnnouncementBar />
          )}
          <NavBar />
          <div className="content">
            {children}
          </div>
          <div className="footer">
            <PageFooter />
          </div>
        </div>
      </div>
    </LoadingOverlay>
  );
};

BasePage.propTypes = {
  children: PropTypes.object.isRequired,
  isLoading: PropTypes.bool,
};

export default BasePage;
