import './buchi-checklist.scss';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import commerce from '../../lib/commerce';
import {
  Checkbox,
  Stack,
} from '@mui/material';
import { useDarkMode } from '../../hooks/bubuchichis-hook';

const BuchiCheckbox = ({ checked, indeterminate, onChange, defaultChecked }) => {
  const isDarkMode = useDarkMode()[0];
  const handleChange = () => {
    onChange();
  };
  return (
    <Checkbox
      checked={checked}
      indeterminate={indeterminate}
      onChange={handleChange}
      defaultChecked={defaultChecked}
      sx={{
        color: isDarkMode ? '#AAAAAA' : '#334748',
        '&.Mui-checked': {
          color: isDarkMode ? '#2fbdff' : '#5fadb3',
        },
        '&.MuiCheckbox-indeterminate': {
          color: isDarkMode ? '#2fbdff' : '#5fadb3',
        },
      }}
    />
  );
};

BuchiCheckbox.propTypes = {
  checked: PropTypes.bool.isRequired,
  indeterminate: PropTypes.bool,
  onChange: PropTypes.func.isRequired,
  defaultChecked: PropTypes.bool,
};

const BuchiChecklist = ({ parentCategory, selectionSet, setSelectionSet }) => {
  const [categories, setCategories] = useState([]);
  useEffect(() => {
    fetchCategories();
  }, []);
  const fetchCategories = () => {
    commerce.categories.retrieve(parentCategory.id, { depth: 3 }).then(response => {
      setCategories(response.children);
    }).catch((error) => {
      // eslint-disable-next-line
      console.log('There was an error fetching the categories', error);
    });
  };
  const handleSetSelectionSet = newSet => {
    setSelectionSet(newSet);
  };
  const handleClick = category => {
    const newCatSet = new Set(Array.from(selectionSet));
    if (selectionSet.has(category)) {
      handleRemoveFromSet(newCatSet, category);
    } else {
      handleAddToSet(newCatSet, category);
    }
    handleSetSelectionSet(newCatSet);
  };
  const handleAddToSet = (set, category) => {
    set.add(category);
  };
  const handleRemoveFromSet = (set, category) => {
    set.delete(category);
  };
  const renderFilterTree = (category, level) => {
    if (category.hasOwnProperty('children') && category.children.length > 0) {
      return (
        <div key={`${category.id}-tree`}>
          {renderFilterParentItem(category, level)}
          {category.children.map(childCat => renderFilterTree(childCat, level + 1))}
        </div>
      );
    }
    return renderFilterItem(category, level);
  };
  const renderFilterParentItem = (category, level) => {
    return (
      <div key={`${category.id}-item`} className="filter-item" style={{ paddingLeft: level * 20 }}>
        <BuchiCheckbox
          checked={
            selectionSet instanceof Set &&
            (
              selectionSet.has(category.slug) ||
              Array.from(category.children).every(child => {
                return selectionSet.has(child.slug);
              })
            ) ||
            false
          }
          indeterminate={
            selectionSet instanceof Set &&
            Array.from(category.children).some(child => {
              return !selectionSet.has(child.slug);
            }) &&
            Array.from(category.children).some(child => {
              return selectionSet.has(child.slug);
            }) ||
            false
          }
          onChange={() => handleClick(category.slug)}
        />
        <div style={{ paddingTop: 9 }}>{category.name}</div>
      </div>
    );
  };
  const renderFilterItem = (category, level) => {
    return (
      <div key={`${category.id}-item`} className="filter-item" style={{ paddingLeft: level * 20 }}>
        <BuchiCheckbox
          checked={
            selectionSet instanceof Set &&
            selectionSet.has(category.slug) ||
            false
          }
          onChange={() => handleClick(category.slug)}
        />
        <div style={{ paddingTop: 9 }}>{category.name}</div>
      </div>
    );
  };
  return (
    <Stack key={`${parentCategory.id}-checklist`}>
      <div key={`${parentCategory.id}-title`}>Filter by {parentCategory.name}:</div>
      {categories.length > 0 && categories.map(category => renderFilterTree(category, 0))}
    </Stack>
  );
};

BuchiChecklist.propTypes = {
  parentCategory: PropTypes.object.isRequired,
  selectionSet: PropTypes.instanceOf(Set).isRequired,
  setSelectionSet: PropTypes.func.isRequired,
};

export default BuchiChecklist;
