import './product-list-container.scss';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import BuchiBreadcrumbs from '../../../component/BuchiBreadcrumbs/buchi-breadcrumbs';
import BuchiChecklist from '../../../component/BuchiChecklist/buchi-checklist';
import BuchiImage from '../../../component/BuchiImage/buchi-image';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import commerce from '../../../lib/commerce';
import lodash from 'lodash';
import {
  Button,
  Pagination,
  PaginationItem,
  Stack,
} from '@mui/material';
import { Grid } from '@material-ui/core';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useWindowDimension } from '../../../hooks/bubuchichis-hook';

const ProductCard = ({ product, onSignalIsLoaded }) => {
  const [isHover, setIsHover] = useState(false);
  const [loadCount, setLoadCount] = useState(0);
  const navigate = useNavigate();
  const LOAD_COUNT = 2;
  const redirectToProductPage = () => {
    const path = `/product/${product.name.replace(/\s+/g, '-')}-i.${product.permalink}`;
    navigate(path, { replace: true });
  };
  useEffect(() => {
    setLoadCount(num => num + 1);
  }, []);
  useEffect(() => {
    if (loadCount >= LOAD_COUNT) {
      onSignalIsLoaded();
    }
  }, [loadCount]);
  return (
    <div
      className={`product-card${isHover ? ' hover' : ''}`}
      onMouseEnter={() => setIsHover(true)}
      onMouseLeave={() => setIsHover(false)}
      onClick={() => redirectToProductPage()}
      onKeyDown={() => redirectToProductPage()}
      tabIndex="0"
      role="menuitem"
    >
      <div className="product-card__image-container">
        <BuchiImage className={`product-card__image${isHover ? ' hover' : ''}`} src={product.image?.url} alt={product.name} handleSignalIsLoaded={() => setLoadCount(num => num + 1)} />
      </div>
      <div className={`product-card__name${isHover ? ' hover' : ''}`}>{product.name}</div>
      <div className={`product-card__price${isHover ? ' hover' : ''}`}>{product.price?.formatted_with_code}</div>
    </div>
  );
};

ProductCard.propTypes = {
  product: PropTypes.object.isRequired,
  onSignalIsLoaded: PropTypes.func,
};

/** contains product grids */
const ProductGrids = ({ products, handleSignalIsLoaded }) => {
  const [loadedItemNum, setLoadedItemNum] = useState(0);
  let productsProcessed = [];
  const width = useWindowDimension()[0];
  const productsPerRow = width < 780 ? 2 : 4;
  const productCardSize = width < 780 ? 6 : 3;
  for (let i = 0; i < products.length; i += productsPerRow) {
    productsProcessed = productsProcessed.concat([products.slice(i, i + productsPerRow)]);
  }
  useEffect(() => {
    setLoadedItemNum(0);
  }, [loadedItemNum]);
  useEffect(() => {
    if (products.length && loadedItemNum >= products.length && handleSignalIsLoaded) {
      handleSignalIsLoaded();
    }
  }, [loadedItemNum]);

  return (
    <div className="product-list-container">
      <Grid container spacing={2}>
        {productsProcessed.length === 0 && (
          <div>
            This collection is empty.
          </div>
        )}
        {productsProcessed.map(row => (
          <Grid item container className="product-row" xs={12} spacing={2} key={`${row[0].id}_row`}>
            {row.map(item => (
              <Grid item xs={productCardSize} key={item.id}>
                <ProductCard product={item} onSignalIsLoaded={() => setLoadedItemNum(num => num + 1)} />
              </Grid>
            ))}
          </Grid>
        ))}
      </Grid>
    </div>
  );
};

ProductGrids.propTypes = {
  products: PropTypes.array.isRequired,
  handleSignalIsLoaded: PropTypes.func,
};

const PrevButtonEl = () => {
  return (
    <div className="pagination-el__button pagination-el__button__container pagination-el__button__container__prev">
      <ArrowBackIcon style={{ display: 'block' }} />
      <div style={{ paddingTop: 2, paddingLeft: 5 }}>Prev</div>
    </div>
  );
};

const NextButtonEl = () => {
  return (
    <div className="pagination-el__button pagination-el__button__container pagination-el__button__container__next">
      <div style={{ paddingTop: 2, paddingRight: 5 }}>Next</div>
      <ArrowForwardIcon style={{ display: 'block' }} />
    </div>
  );
};

const ProductListContainer = ({ onSetIsLoaded }) => {
  const [products, setProducts] = useState([]);
  // const [categories, setCategories] = useState([]);
  const [paginationData, setPaginationData] = useState({});
  const [selectedCategories, setSelectedCategories] = useState(new Set());
  const handleSetSelectedCategories = newSet => {
    setSelectedCategories(newSet);
  };
  const t = useTranslation()[0];
  const location = useLocation();
  const navigate = useNavigate();
  const basePagination = { per_page: 28 };
  useEffect(() => {
    const search = location.search;
    const params = new URLSearchParams(search);
    const pagination = {
      ...basePagination,
      current_page: params.get('page') || 1,
    };
    setPaginationData(pagination);
    const tempCat = params.get('cat');
    if (tempCat && typeof tempCat === 'string') {
      setSelectedCategories(new Set(tempCat.split(',')));
    } else {
      setSelectedCategories(new Set(tempCat));
    }
    // if (categories.length === 0) {
    //   fetchCategories();
    // }
    fetchProducts(pagination.per_page, pagination.current_page);
  }, []);

  // const fetchCategories = () => {
  //   commerce.categories.list().then(response => {
  //     // eslint-disable-next-line
  //     console.log(response)
  //     setCategories(response.data);
  //   }).catch((error) => {
  //     // eslint-disable-next-line
  //     console.log('There was an error fetching the categories', error);
  //   });
  // };

  const fetchProducts = (limit, page) => {
    commerce.products.list({
      limit,
      page, // The page number defaults to 1 if it is not specified
      category_slug: Array.from(selectedCategories),
    }).then(response => {
      setProducts(response.data || []);
      if (response.data) {
        setProducts(response.data);
      } else {
        setProducts([]);
        onSetIsLoaded(true);
      }
      setPaginationData(response.meta.pagination);
    }).catch((error) => {
      // eslint-disable-next-line
      console.log('There was an error fetching the products', error);
    });
  };

  const handlePageChange = (e, pageNum) => {
    if (!lodash.isNull(e)) {
      e.stopPropagation();
    }
    handleRedirect(pageNum);
  };

  const handleFilterSubmitClick = () => {
    handleRedirect(1);
  };

  const handleRedirect = pageNum => {
    const itemsPerPage = paginationData.per_page;
    onSetIsLoaded(false);
    setProducts([]);
    setPaginationData(basePagination);
    const params = getRedirectParams(pageNum);
    navigate(`/products?${params}`, { replace: true });
    fetchProducts(itemsPerPage, pageNum);
  };

  const getRedirectParams = pageNum => {
    const params = new URLSearchParams();
    params.set('page', pageNum);
    if (selectedCategories.size > 0) {
      params.set('cat', Array.from(selectedCategories));
    }
    return params;
  };

  const breadcrumbs = [
    {
      name: 'homePage',
      label: t('navBar.homePage'),
      path: '/',
    },
    {
      name: 'productPage',
      label: t('navBar.productPage'),
      path: '/products',
    },
  ];

  const categories = [
    {
      id: 'cat_ypbroEMdmo8n4e',
      name: 'Category',
    },
    {
      id: 'cat_RyWOwmDb6lnEa2',
      name: 'Product Type',
    },
  ];

  return (
    <Grid container className="page-width">
      <Grid item xs={12}>
        <BuchiBreadcrumbs breadcrumbs={breadcrumbs} />
      </Grid>
      <Grid item xs={12}>
        <h1 className="page-title">Our Products</h1>
      </Grid>
      <Grid item xs={12}>
        <div style={{ marginBottom: 20 }}>Find products with all the selected criteria:</div>
        <Stack direction="row" spacing={2}>
          {categories.length > 0 && categories.map(category => (
            <BuchiChecklist
              key={`${category.id}-buchiChecklist`}
              parentCategory={category}
              selectionSet={selectedCategories}
              setSelectionSet={handleSetSelectedCategories}
            />
          ))}
        </Stack>
        <Button onClick={() => handleFilterSubmitClick()}>
          FILTER
        </Button>
      </Grid>
      <Grid item xs={12}>
        <ProductGrids
          products={products}
          handleSignalIsLoaded={() => onSetIsLoaded(true)}
        />
      </Grid>
      <Grid item xs={12} style={{ display: 'flex', justifyContent: 'center' }}>
        <Pagination
          page={parseInt(paginationData?.current_page, 10) || 1}
          count={paginationData?.total_pages || 1}
          onChange={handlePageChange}
          renderItem={item => (
            <PaginationItem
              components={{
                previous: PrevButtonEl,
                next: NextButtonEl,
              }}
              className="pagination-el__button"
              {...item}
            />
          )}
        />
      </Grid>
      {/* {isRedirect && (
        // <Navigate to={redirectPath} />
        <div>{redirectPath} {paginationData?.total_pages}</div>
      )} */}
      <div style={{ minHeight: 20 }} />
    </Grid>
  );
};

ProductListContainer.propTypes = {
  onSetIsLoaded: PropTypes.func.isRequired,
};

export default ProductListContainer;
