import './buchi-breadcrumbs.scss';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import PropTypes from 'prop-types';
import React from 'react';
import {
  Breadcrumbs,
  Typography,
} from '@mui/material';
import { Link } from 'react-router-dom';

/**
 * BuchiBreadcrumbs, based on MUI Breadcrumbs
 * @param breadcrumbs
 *    Members must be of the format:
 *    { name, label, path }
 * @returns bubuchichis breadcrumb component
 */
const BuchiBreadcrumbs = ({ breadcrumbs }) => {
  const breadcrumbsLength = breadcrumbs.length;
  const processedBreadcrumbs = breadcrumbs.map((item, i) => {
    if (i + 1 < breadcrumbsLength) {
      return (
        <Link className="buchi-breadcrumbs__link" underline="hover" key={item.name} to={item.path}>
          {item.label}
        </Link>
      );
    }
    return (
      <Typography className="buchi-breadcrumbs__text" key={item.name}>
        {item.label}
      </Typography>
    );
  });
  return (
    <Breadcrumbs
      className="buchi-breadcrumbs"
      separator={<NavigateNextIcon className="buchi-breadcrumbs__separator" fontSize="small" />}
      aria-label="breadcrumb"
    >
      {processedBreadcrumbs}
    </Breadcrumbs>
  );
};

BuchiBreadcrumbs.propTypes = {
  breadcrumbs: PropTypes.array.isRequired,
};

export default BuchiBreadcrumbs;
