import './cart-container.scss';
import BuchiImage from '../../../component/BuchiImage/buchi-image';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import commerce from '../../../lib/commerce';
import lodash from 'lodash';
import {
  Button,
  Card,
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Grid,
  Radio,
  RadioGroup,
} from '@material-ui/core';
import { useParams } from 'react-router-dom';

const CartItem = ({ item, onUpdateCartQty, onRemoveFromCart }) => {
  const handleUpdateCartQty = quantity => {
    onUpdateCartQty(item.id, quantity);
  };
  const handleRemoveFromCart = () => {
    onRemoveFromCart(item.id);
  };
  return (
    <div className="cart-item">
      <img className="cart-item__image" src={item.image.url} alt={item.name} style={{ width: 200 }} />
      <div className="cart-item__details">
        <h4 className="cart-item__details-name">{item.name}</h4>
        <div className="cart-item__details-qty">
            <button type="button" onClick={() => handleUpdateCartQty(item.id, item.quantity - 1)}>-</button>
            <p>{item.quantity}</p>
            <button type="button" onClick={() => handleUpdateCartQty(item.id, item.quantity + 1)}>+</button>
        </div>
        <div className="cart-item__details-price">{item.line_total.formatted_with_symbol}</div>
      </div>
      <button
        type="button"
        className="cart-item__remove"
        onClick={() => handleRemoveFromCart()}
      >
        Remove
      </button>
    </div>
  );
};

CartItem.propTypes = {
  item: PropTypes.object,
  onUpdateCartQty: PropTypes.func,
  onRemoveFromCart: PropTypes.func,
};

const CartContainer = ({ onSignalIsLoaded, onEmptyCart }) => {
  const [cart, setCart] = useState({});
  useEffect(() => {
    fetchCart();
  }, []);

  const fetchCart = () => {
    commerce.cart.retrieve().then(retrievedCart => {
      setCart(retrievedCart);
      onSignalIsLoaded();
    }).catch((error) => {
      // eslint-disable-next-line
      console.log('There was an error fetching the cart', error);
    });
  };

  const handleUpdateCartQty = (lineItemId, quantity) => {
    commerce.cart.update(lineItemId, { quantity }).then(response => {
      setCart(response.cart);
    }).catch((error) => {
      // eslint-disable-next-line
      console.log('There was an error updating the cart items', error);
    });
  };

  const handleRemoveFromCart = (lineItemId) => {
    commerce.cart.remove(lineItemId).then((resp) => {
      setCart(resp.cart);
    }).catch((error) => {
      // eslint-disable-next-line
      console.error('There was an error removing the item from the cart', error);
    });
  };

  const handleEmptyCart = () => {
    commerce.cart.empty().then((resp) => {
      setCart(resp.cart);
    }).catch((error) => {
      // eslint-disable-next-line
      console.error('There was an error emptying the cart', error);
    });
  };

  const renderEmptyCartMessage = () => {
    if (cart.total_unique_items > 0) {
      return <div />;
    }

    return (
      <p className="cart__none">
        You have no items in your shopping cart, start adding some!
      </p>
    );
  };

  const renderItems = () => (
    cart?.line_items?.map((lineItem) => (
      <CartItem
        item={lineItem}
        onUpdateCartQty={handleUpdateCartQty}
        onRemoveFromCart={handleRemoveFromCart}
        key={lineItem.id}
        className="cart__inner"
      />
    ))
  );

  const renderTotal = () => (
    <div className="cart__total">
      <p className="cart__total-title">Subtotal:</p>
      <p className="cart__total-price">{cart?.subtotal?.formatted_with_symbol}</p>
    </div>
  );

  return (
    <div className="page-width">
      <Grid item xs={12}>
        <h1 className="page-title">Cart</h1>
      </Grid>
      <Grid>
        { renderEmptyCartMessage() }
        { renderItems() }
        { renderTotal() }
      </Grid>
      <h1>TEST</h1>
      {(lodash.isEmpty(cart) || cart.total_items === 0) && (
        <p>Your Cart is Empty.</p>
      )}
      {(!lodash.isEmpty(cart) && cart.total_items > 0) && (
        <div>{ JSON.stringify(cart) }</div>
      )}
    </div>
  );
};
CartContainer.propTypes = {
  onSignalIsLoaded: PropTypes.func,
  onEmptyCart: PropTypes.func,
};

export default CartContainer;
