import './nav-bar.scss';
import AppsIcon from '@mui/icons-material/Apps';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import HomeIcon from '@mui/icons-material/Home';
import MenuIcon from '@mui/icons-material/Menu';
import PersonOutlineOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import SearchOutlinedIcon from '@material-ui/icons/SearchOutlined';
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';
import titleLogo from '../../img/Logo.PNG';
// import { Box } from '@mui/material';
import {
  Box,
  Button,
  Divider,
  Drawer,
  Grid,
} from '@material-ui/core';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import {
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import { makeStyles } from '@material-ui/core/styles';
import { styled } from '@mui/material/styles';
import { useDarkMode, useWindowDimension } from '../../hooks/bubuchichis-hook';
import { useTranslation } from 'react-i18next';

const navBarLinkItemList = [
  {
    name: 'homePage',
    label: 'navBar.homePage',
    path: '/',
    icon: <HomeIcon className="icon icon-sidebar" />,
  },
  {
    name: 'productPage',
    label: 'navBar.productPage',
    path: '/products',
    icon: <AppsIcon className="icon icon-sidebar" />,
  },
  // {
  //   name: 'informationPage',
  //   label: 'navBar.informationPage',
  //   path: '/',
  // },
  // {
  //   name: 'aboutUsPage',
  //   label: 'navBar.aboutUsPage',
  //   path: '/',
  // },
  // {
  //   name: 'contactPage',
  //   label: 'navBar.contactPage',
  //   path: '/',
  // },
];
const NavLogo = () => {
  return (
    <div className={'site-header__logo'}>
      <a href="#/" className={'site-header__logo-image'}>
        <img
          src={titleLogo}
          alt="bubuchichis"
          style={{ maxWidth: '100px' }}
        />
      </a>
    </div>
  );
};

const CentreNavContainer = styled('div')(({ theme }) => ({
  padding: theme.spacing(1),
  [theme.breakpoints.down('sm')]: {
    display: 'none',
  },
}));

const CentreNav = () => {
  const t = useTranslation()[0];
  return (
    <CentreNavContainer className="site-nav list--inline">
      {navBarLinkItemList.map((item, i) => {
        const isActive = false;
        return (
          <li key={item.label}>
            <Link className={isActive ? 'site-nav__link site-nav__link--active' : 'site-nav__link'} to={item.path}>
              <span className="site-nav__label">{t(`${item.label}`)}</span>
            </Link>
          </li>
        );
      })}
    </CentreNavContainer>
  );
};

const SideBarButton = styled(Button)(({ theme }) => ({
  padding: theme.spacing(1),
  [theme.breakpoints.up('sm')]: {
    display: 'none',
  },
}));
const SideBar = () => {
  const t = useTranslation()[0];
  const isDarkMode = useDarkMode()[0];
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const width = useWindowDimension()[0];
  useEffect(() => {
    if (width > 600) {
      setIsDrawerOpen(false);
    }
  }, [width]);
  return (
    <>
      <SideBarButton className="site-header__icon site-header__menu" onClick={() => setIsDrawerOpen(true)}>
        <MenuIcon className="icon icon-menu" />
      </SideBarButton>
      <Drawer
        anchor={'left'}
        className={`theme--${isDarkMode ? 'dark' : 'default'}`}
        open={isDrawerOpen}
        onClose={() => setIsDrawerOpen(false)}
      >
        <Box
          sx={{ width: 250 }}
          style={{ height: '100%', paddingRight: 50 }}
          className="site-nav__sidebar"
          role="presentation"
          onClick={() => setIsDrawerOpen(false)}
          onKeyDown={() => setIsDrawerOpen(false)}
        >
          <List>
            {navBarLinkItemList.map((item, i) => {
              const isActive = false;
              return (
                <ListItem
                  component={Link}
                  to={item.path}
                  key={item.name}
                  className={isActive ? 'site-nav__link site-nav__link--active' : 'site-nav__link'}
                >
                  <ListItemIcon className="site-header__icon site-header__sidebar">
                    {item.icon}
                  </ListItemIcon>
                  <ListItemText
                    className="site-nav__label"
                    primary={t(`${item.label}`)}
                  />
                </ListItem>
              );
            })}
          </List>
          {/* <Divider />
          <List>
            {['All mail', 'Trash', 'Spam'].map((text, index) => (
              <ListItem key={text} disablePadding>
                <ListItemButton>
                  <ListItemIcon>
                    {index % 2 === 0 ? <DarkModeIcon /> : <ShoppingCartOutlinedIcon />}
                  </ListItemIcon>
                  <ListItemText primary={text} />
                </ListItemButton>
              </ListItem>
            ))}
          </List> */}
        </Box>
      </Drawer>
    </>
  );
};

const NavBar = () => {
  const [t, i18n] = useTranslation();
  const navigate = useNavigate();
  const currLocation = useLocation();
  const navigateTo = (dest) => {
    if (dest !== currLocation.pathname) {
      navigate(dest);
    } else {
      window.location.reload();
    }
  };
  const toggleDarkMode = useDarkMode()[1];
  return (
    <header className="site-header sticky" role="banner">
      <Grid container spacing={0} className={'grid site-header__mobile-nav'} alignItems="center">
        <Grid item xs={6} sm={3}>
          <div className={'site-header__icons-wrapper-left'}>
            <SideBar />
            <NavLogo />
          </div>
        </Grid>
        <Grid item xs={1} sm={6} role="navigation">
          <CentreNav />
        </Grid>
        <Grid item xs={5} sm={3} className={'site-header__icons text-right'}>
          <div className={'site-header__icons-wrapper'}>
            {/* <a href="#/" className="site-header__icon site-header__search">
              <SearchOutlinedIcon className="icon icon-search" />
              <span className="icon__fallback-text">Search</span>
            </a> */}
            {/* <a href="#/account/login" className="site-header__icon site-header__account">
              <PersonOutlineOutlinedIcon className="icon icon-login" />
              <span className="icon__fallback-text">Log in</span>
            </a> */}
            <Button onClick={() => toggleDarkMode()} className="site-header__icon site-header__darkmode">
              <DarkModeIcon className="icon icon-dark-mode" />
            </Button>
            <a href="#/cart" className="site-header__icon site-header__cart">
              <ShoppingCartOutlinedIcon className="icon icon-cart" />
              {/* <span className="icon__fallback-text">Cart</span> */}
            </a>
          </div>
        </Grid>
      </Grid>
    </header>
  );
};

export default NavBar;
