import './product-container.scss';
import BuchiBreadcrumbs from '../../../component/BuchiBreadcrumbs/buchi-breadcrumbs';
import BuchiImage from '../../../component/BuchiImage/buchi-image';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import commerce from '../../../lib/commerce';
import lodash from 'lodash';
import remarkGfm from 'remark-gfm';
import { Button } from '@mui/material';
import {
  Card,
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Grid,
  Radio,
  RadioGroup,
} from '@material-ui/core';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const currencyDict = {
  sgd: {
    label: 'SGD',
    exchangeRate: 1,
  },
  usd: {
    label: 'USD',
    exchangeRate: 0.76,
  },
};

const getIdFromProductId = (productId) => {
  const paramArr = productId.split('-');
  const itemCodeArr = paramArr[paramArr.length - 1].split('.');
  return itemCodeArr[1];
};

const RadioButton = ({ disabled, ...props }) => {
  return (
    <Radio
      className="radio-button"
      disableRipple
      aria-disabled={disabled}
      color="default"
      checkedIcon={<div />}
      icon={<div />}
      {...props}
    />
  );
};

RadioButton.propTypes = {
  disabled: PropTypes.bool,
};

const VariantRadioField = ({ name, currVariantIndex, variants, handleVariantChange }) => {
  return (
    <FormControl>
      <FormLabel className="options-title">
        <p>{name}:</p>
      </FormLabel>
      <RadioGroup
        className="options-container"
        value={currVariantIndex}
        aria-labelledby="variant-radios"
        name="variant-radios"
        onChange={handleVariantChange}
      >
        {variants.map((variant, i) => {
          let labelClassSuffix = '__default';
          if (variant.isSoldOut) {
            labelClassSuffix = '__disabled';
          } else if (i === currVariantIndex) {
            labelClassSuffix = '__selected';
          }
          return (
            <FormControlLabel
              className="no-radio-option"
              key={variant.id}
              // value={!variant.isSoldOut ? i : 'disabled'}
              value={i}
              control={<RadioButton
                // disabled={variant.isSoldOut}
              />}
              label={
                <p className={`option-label option-label${labelClassSuffix}`}>
                  {variant.name}
                </p>
              }
            />
          );
        })}
      </RadioGroup>
    </FormControl>
  );
};

VariantRadioField.propTypes = {
  name: PropTypes.string.isRequired,
  currVariantIndex: PropTypes.number.isRequired,
  variants: PropTypes.array.isRequired,
  handleVariantChange: PropTypes.func.isRequired,
};

const ProductContainer = () => {
  const [product, setProduct] = useState({});
  const { productId } = useParams();
  const t = useTranslation()[0];
  useEffect(() => {
    const sku = getIdFromProductId(productId);
    if (sku) {
      commerce.products.retrieve(
        sku,
        { type: 'permalink' },
      ).then(response => {
        // eslint-disable-next-line
        console.log(response);
        setProduct(response);
      });
    }
  }, []);
  const [currVariantIndex, setCurrVariantIndex] = useState(-1);
  const handleAddToCart = quantity => {
    commerce.cart.add(product.id, quantity).then((item) => {
      // setCart(item.cart);
    }).catch((error) => {
      // eslint-disable-next-line
      console.error('There was an error adding the item to the cart', error);
    });
  };
  // temp const
  const currCurrency = 'sgd';
  const getPriceRangeString = (currency, type, variants) => {
    if (!(currency in currencyDict)) {
      return 'invalidCurrency error';
    }
    let maxPrice = -1;
    let minPrice = 99999;
    for (let i = 0; i < variants.length; i += 1) {
      const currPrice = variants[i].price[type] * currencyDict[currency].exchangeRate;
      maxPrice = maxPrice < currPrice ? currPrice : maxPrice;
      minPrice = minPrice > currPrice ? currPrice : minPrice;
    }
    if (maxPrice < 0) {
      return 'Not Available';
    }
    const currencyString = currencyDict[currency].label;
    return `${currencyString}${minPrice.toFixed(2)} - ${currencyString}${maxPrice.toFixed(2)}`;
  };
  const fullPriceRangeString = product?.variants ? getPriceRangeString(currCurrency, 'full', product.variants) : '';
  const discPriceRangeString = product?.variants ? getPriceRangeString(currCurrency, 'discounted', product.variants) : '';
  const [currFullPriceString, setCurrFullPriceString] = useState(fullPriceRangeString);
  const handleVariantChange = e => {
    // // eslint-disable-next-line
    // console.log(e);
    e.stopPropagation();
    let value = currVariantIndex;
    // let fullPriceString = currFullPriceString;
    if (e.target.value !== 'disabled') {
      value = parseInt(e.target.value, 10);
    //   const fullPriceMagnitude = 0;
    //   // const fullPriceMagnitude = (product?.variants[value].price.full * currencyDict[currCurrency].exchangeRate).toFixed(2);
    //   fullPriceString = `${currencyDict[currCurrency].label}${fullPriceMagnitude}`;
    }
    setCurrVariantIndex(value);
    // setCurrFullPriceString(fullPriceString);
  };
  const breadcrumbs = [
    {
      name: 'homePage',
      label: t('navBar.homePage'),
      path: '/',
    },
    {
      name: 'productPage',
      label: t('navBar.productPage'),
      path: '/products',
    },
    {
      name: product.name,
      label: product.name,
      path: `/product/${product.name}-i.${product.permalink}`,
    },
  ];
  return (
    <div className="page-width">
      {!lodash.isEmpty(product) && (
      <>
        <Grid item xs={12}>
          <BuchiBreadcrumbs breadcrumbs={breadcrumbs} />
        </Grid>
        <Grid container className="product-container" spacing={2}>
          <Grid item sm={6} xs={12}>
            <BuchiImage className="product-image" src={product.image?.url} alt={product.name} />
          </Grid>
          <Grid item sm={6} xs={12}>
            <Grid container className="product-info" style={{ marginLeft: '30px', top: '110px' }}>
              <Grid item xs={12}>
                <h1 className="page-title product-title">{product.name}</h1>
              </Grid>
              <Grid item xs={12}>
                <div className="price--large">
                  <span>{product.price.formatted_with_code}</span>
                </div>
              </Grid>
              <Grid item xs={12}>
                {product.variant_groups.map(variant => (
                  <VariantRadioField name={variant.name} currVariantIndex={currVariantIndex} variants={variant.options} handleVariantChange={handleVariantChange} />
                ))}
              </Grid>
              <Grid item xs={12}>
                <Button
                  className="product__btn"
                  onClick={() => handleAddToCart(1)}
                >
                  ADD
                </Button>
              </Grid>
              <Grid item xs={12}>
                <div className="product-description">
                  <br />
                  {
                    // eslint-disable-next-line
                    <div dangerouslySetInnerHTML={{__html: product.description}} />
                  }
                </div>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </>
      )}
    </div>
  );
};

export default ProductContainer;
